.page-hero-container img {
    object-fit: cover;
    width: 100%;
    height: 60vh;
    position: absolute;
    z-index: -1;
 
}

.page-hero-container{
  
    /* background: url('../../images/img-home.jpg') center center/cover
    no-repeat; */
    height: 60vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
margin-bottom: 10px;
text-decoration: none;
font-family: 'Raleway', sans-serif;
}

.page-hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin: 0 auto;

    max-width: 80%;
text-align: center;
}

.page-hero-container > p{
    margin-top: 8px;
    margin-bottom: 24px;
    display: flex;
    flex-direction: center;
    color: #fff;
   
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

@media screen and (max-width: 960px) {
    .page-hero-container > h1{

         font-size: 60px;
         
     }

    .page-hero-container > p{
       margin-left: 20px;
       margin-right: 20px;
        font-size: 24px;
        
    }

}